.container{
    margin: 0 auto;
    padding: 40px 50px 62px 50px
}

.heading{
    font-family: Playfair Display;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.333333px;
    color: #2D2D41; 
    margin-top: 0;
    margin-bottom: 42px; 
}

.payment-form{
    margin-bottom: 56px;
}

.title{
    margin-top: 0px;
    margin-bottom: 20px;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.333333px;
    color: #2D2D41;
}

.radioBtn, .list-item{
    cursor: pointer;    
}

.list{
    list-style: none;
    margin-top: 0;
    padding-left: 0;
    display: flex;
    width: 50%;
}

.delivery-form{
    margin-bottom: 40px;
}

.list.delivery, .list.input{
    flex-wrap: wrap;
}

.list .list-item:nth-child(3), .list .list-item:nth-child(4){
    margin-top: 13px;
}


.list .list-item:nth-child(3), .list .list-item:first-child{
    margin-right: 16px;
}

.list-item{
    background: #F8F8F8;
    width: 311px;
    height: 62px;
    display: flex;
    justify-content: center;
    align-items: center;    
}

.radio{
    -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        position: absolute;
}

.text{
    color: #2D2D41;
    letter-spacing: -0.333333px;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
}

.form-input{
    width: 311px;
    height: 40px;
    border: 1px solid #EDEFF4; 
    box-sizing: border-box;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-right: 15px;
}


.form-item:not(:last-child){
    margin-bottom: 17px;
}

textarea{
    width: 641px;
    height: 95px;
    resize: none;
    border: 1px solid #EDEFF4; 
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 10px;
    box-sizing: border-box;
    margin-bottom: 32px;
}

.submitBtn{
    width: 352px;
    height: 45px;
    border: none;
    background-color: #517DA3;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #fff;
}

